import React, { useEffect, useState } from 'react'
import { useSelector, } from 'react-redux'
import { AVAILABLE_ASSETS, BlockPopup, RPC_URL_BY_CHAIN, EXPLORER_TXN_URL_LIST, ToastPopup, addressRegex, API_URL, SITE_LINK, IMAGE_URL } from '../../actions/constant'
import truncateEthAddress from 'truncate-eth-address'
import TextField from '../common/TextField'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import ConnectWallet from "../WalletConnect/web3modal"

import { getCoinBalance, getMarketPrice, getTokenBalance, sendCoin, sendTokenFromContract } from '../../actions/common'



const ViewQrCodePayment = () => {

    const { chainId, connected, signer, provider, address } = useSelector(state => state.web3)
    const { user } = useSelector(state => state.auth)

    const { userID, walletAddress } = useParams()

    const [data, setdata] = useState({})
    const [setting, setsetting] = useState([])

    const [selectedasset, setselectedasset] = useState(null)
    const [selectedtype, setselectedtype] = useState(null)
    const [filterAsset, setfilterAsset] = useState([])
    const [assetBalance, setAssetBalance] = useState(0)
    const [currentPrice, setcurrentPrice] = useState(0)
    const [balloading, setballoading] = useState(false)
    //
    const [txnStatus, settxnStatus] = useState({})

    const [sendToken, setSendToken] = useState(0)
    const [payAmount, setpayAmount] = useState("")


    useEffect(() => {

        if (selectedasset && connected) {
            getBalance()
            const findType = AVAILABLE_ASSETS.find(x => x.value === selectedasset && x.networkID === parseInt(chainId))
            setselectedtype(findType.type)
        }

    }, [selectedasset, connected])



    useEffect(() => {
        getData()
    }, [])

    const getData = async () => {
        BlockPopup.standard('.viewqrcode', `Please wait...`);
        try {
            let result = await axios.post(API_URL + `/api/usersetting/getbyuserID`, { userID })
            BlockPopup.remove('.viewqrcode');
            if (result.data) {
                setdata(result.data)
                setsetting(result.data)

            }

        } catch (err) {
            BlockPopup.remove('.viewqrcode');
            ToastPopup.failure("Error Ocuured in API")
        }
    }


    useEffect(() => {
        if (Object.keys(data).length > 0) {
            filterAssets()
        }

    }, [connected, data])

    const filterAssets = () => {
        const filterValue = AVAILABLE_ASSETS.filter(x => x.networkID === parseInt(chainId))

        if (filterValue.length > 0) {
            console.log("filterValue", filterValue[0].type)
            setselectedasset(filterValue[0].value)
            setselectedtype(filterValue[0].type)
            setfilterAsset(filterValue)
        }
    }

    var filterAssetRender = filterAsset.map((result, index) => {
        return <option value={result.value}>{result.label}</option>
    })


    useEffect(() => {
        if (payAmount !== "" && parseFloat(payAmount) > 0 && parseFloat(currentPrice) > 0) {
            let sendAmounteCalc = parseFloat(payAmount) / parseFloat(currentPrice)
            setSendToken(sendAmounteCalc.toString())
        } else {
            setSendToken(0)
        }
    }, [payAmount, selectedasset, currentPrice])

    const getBalance = async () => {
        setAssetBalance(0)
        setcurrentPrice(0)
        setballoading(true)
        let findasset = AVAILABLE_ASSETS.find(x => x.value === selectedasset && x.networkID === parseInt(chainId))
        if (findasset && findasset.type === "coin") {
            let getBal = await getCoinBalance(signer)
            let currentprice = await getMarketPrice(findasset.ids, setting.defaultCurrency)

            setAssetBalance(getBal)
            setcurrentPrice(currentprice)
            setballoading(false)
        } else if (findasset && findasset.type === "token") {
            console.log("findasset.contractAddress", findasset.contractAddress)
            let getBal = await getTokenBalance(findasset.contractAddress, address, signer)
            let currentprice = await getMarketPrice(findasset.ids, setting.defaultCurrency)

            setAssetBalance(getBal)
            setcurrentPrice(currentprice)
            setballoading(false)

        }
    }







    var logoImg = "assets/images/logo.png"
    if (setting.qrLogo) {
        logoImg = IMAGE_URL + setting.qrLogo
    }


    const triggerSend = async () => {
        if (sendToken === "" || payAmount === "") {
            ToastPopup.failure("Fill All the Fields")
            return
        }
        // Regular expression pattern to match Ethereum wallet address
        if (!addressRegex.test(walletAddress)) {
            ToastPopup.failure("Invalid Sender ddress.Address start with 0x")
            return
        }
        if (parseFloat(payAmount) <= 0) {
            ToastPopup.failure("Payment must be greater the zero")
            return
        }
        if (parseFloat(sendToken) <= 0) {
            ToastPopup.failure("Send token must be greater the zero")
            return
        }

        if (parseFloat(sendToken) > parseFloat(assetBalance)) {
            ToastPopup.failure("Low Balance")
            return
        }
        console.log("token", selectedtype)


        BlockPopup.standard('.viewqrcode', `Please wait... Don't Close`);
        if (selectedtype === "coin") {
            const sendResult = await sendCoin(provider, signer, walletAddress, sendToken)
            if (sendResult.status) {
                onSendPayment(sendResult.txhash)
            } else {
                BlockPopup.remove('.viewqrcode');
            }
            settxnStatus(sendResult)


        } else if (selectedtype === "token") {
            let findasset = AVAILABLE_ASSETS.find(x => x.value === selectedasset && x.networkID === parseInt(chainId))
            const sendResult = await sendTokenFromContract(provider, signer, findasset.contractAddress, walletAddress, sendToken)
            if (sendResult.status) {
                onSendPayment(sendResult.txhash)

            } else {
                BlockPopup.remove('.viewqrcode');
            }
            settxnStatus(sendResult)

        }
    }

    const onSendPayment = async (hash) => {
        try {
            const insertData = {
                userID,
                invoiceAmount: payAmount,
                currencySymbol: setting.defaultCurrency,
                assetSymbol: selectedasset,
                assetPrice: currentPrice,
                paidAmount: sendToken,
                payerWalletAddress: address,
                txnHash: hash,
                status: "Paid",
                chainId
            }
            let result = await axios.post(API_URL + "/api/qrpayment", insertData)
            if (result.data) {
                getBalance()
                getData()
                setpayAmount("")
                setSendToken("")
                BlockPopup.remove('.viewqrcode');
            }
        } catch (err) {
            BlockPopup.remove('.viewqrcode');
            ToastPopup.failure("Erorr Occured Try Again")
        }
    }


    return (
        <div>
            <div className="modal-dialog modal-md">
                <div className="modal-content viewqrcode">
                    <div className="modal-body">
                        <div>
                            <div>
                                <div className="row g-0">
                                    <div className="col-md-12">

                                        <div className="p-xl-1 p-3">
                                            <div className="text-center mb-2">

                                                <img
                                                    src={logoImg}
                                                    className="align-self-center"
                                                    alt="logo-img"
                                                    height={70}
                                                />

                                            </div>
                                            <h3 className="h3 mb-0 mt-3 text-center">{setting.qrName}</h3>
                                            <p className="text-muted mt-1 mb-4 text-center">
                                                Effortlessly View and Swiftly Pay Your Crypto Payments!
                                            </p>

                                            <div className='text-center'>
                                                <TextField
                                                    isRequired
                                                    label={`Enter the ${setting.defaultCurrency} Amount You Want to Pay`}
                                                    onChange={(e) => setpayAmount(e.target.value)}
                                                    value={payAmount}
                                                    placeholder=""
                                                // error={errors.sendToken}
                                                />

                                                {/* <p class="text-muted">Amount to Pay</p> */}
                                            </div>


                                            <div className="mb-3">
                                                <div className="row">
                                                    {connected && <div className="col">
                                                        <ul className="checkout-list">
                                                            <li>
                                                                <span>Select Asset</span>
                                                                <span>
                                                                    <select value={selectedasset} onChange={(e) => setselectedasset(e.target.value)} className='form-select'>
                                                                        {filterAssetRender}
                                                                    </select>
                                                                </span>
                                                            </li>
                                                            <li>
                                                                <span>Your Balance</span>
                                                                <span className="checkout-total">{parseFloat(assetBalance).toFixed(4)} {selectedasset}</span>
                                                            </li>
                                                            <li>
                                                                <span>{selectedasset} Current Price</span>
                                                                <span className="checkout-balance text-primary">{currentPrice} {setting.defaultCurrency}</span>
                                                            </li>
                                                            <li>
                                                                <span>{selectedasset} to Send</span>
                                                                <span className="checkout-balance text-primary">{parseFloat(sendToken).toFixed(4)} {selectedasset}</span>
                                                            </li>
                                                            <li>
                                                                <span>Receiver Address</span>
                                                                <span className="checkout-balance text-primary">{walletAddress && truncateEthAddress(walletAddress)}</span>
                                                            </li>


                                                        </ul>
                                                    </div>}



                                                </div>
                                                {txnStatus && txnStatus.status && Object.keys(txnStatus).length > 0 && <div role="alert" class="alert text-success bg-white border alert-dismissible">
                                                    <div class="d-flex align-items-start">
                                                        <span class="badge badge-soft-success align-self-center me-3">Success</span>
                                                        <div class="w-100">Your Payment is successfully done <a href={EXPLORER_TXN_URL_LIST[chainId] + txnStatus.txhash} target="_blank" class="text-success alert-link">View Transaction</a></div>
                                                    </div>
                                                    <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                                </div>}
                                                {(txnStatus && Object.keys(txnStatus).length > 0 && !txnStatus.status) && <div role="alert" class="alert text-danger bg-white border alert-dismissible">
                                                    <div class="d-flex align-items-start">
                                                        <span class="badge badge-soft-danger align-self-center me-3">Ohh no!</span>
                                                        <div class="w-100">Your Payment is failed <a href={EXPLORER_TXN_URL_LIST[chainId] + txnStatus.txhash} target="_blank" class="text-danger alert-link">View Transaction</a></div>
                                                    </div>
                                                    <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
                                                </div>}

                                                {connected ? <React.Fragment>
                                                    <div className="mb-0 text-center d-grid">
                                                        {!balloading ? <button className="btn btn-primary" onClick={() => triggerSend()}>
                                                            Pay Now
                                                        </button> : <button class="btn btn-outline-primary btn-sm" type="button">
                                                            <span class="spinner-border spinner-border-sm me-1" role="status" aria-hidden="true"></span>
                                                            Fetching details..
                                                        </button>}
                                                    </div>
                                                </React.Fragment> :
                                                    <div className='my-3'>
                                                        <ConnectWallet />
                                                    </div>

                                                }
                                            </div>




                                            <div className='poweredby' style={{ textAlign: 'right' }}>
                                                <img
                                                    src="assets/images/poweredby.png"
                                                    className="align-self-end"
                                                    alt="logo-img"
                                                    height={40}
                                                />
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>{" "}
                            {/* end card-body */}
                        </div>
                    </div>
                </div>
            </div>
        </div>

    )
}

export default ViewQrCodePayment
